*{
  -webkit-user-select:none;
  -moz-user-select:none;
  -ms-user-select:none;
  user-select:none;
  font-family: 'Do Hyeon', sans-serif;
  color : #fff;
}

.hidden{
  display: none;
}

.backGround{
  position:absolute;
  filter: brightness(.4);
  opacity:80%;
  z-index: -1;
  background-size : cover;  
}

#clock{
  margin:0;
  padding:150px 0 40px 0;
  font-size: 90px;
}

#login{
  margin: auto;
  padding:10px;
  width: 300px;
  background-color:rgba(255, 255, 255, 0.4);
  border-radius: 20px;
}

#login-input{
  outline-style: none;
  background-color:transparent;
  border: none;
  border-radius: 10px;
  height: 40px;
  width: 200px;
  padding:0;
  font-size:20px; 
  margin-left: 10px;
}

#login-input::placeholder{
  font-size: 16px;
  color:#fff;
}

button{
  background-color:transparent;
  border: none;
  padding: 20px 15px;
  border-radius: 20px;
  cursor: pointer;
  font-size:18px;
}

#greeting{
  margin-top: 0px;
}

#todo-form div{
  font-size: 22px;
}

#todo-input{
  outline-style: none;
  background-color:transparent;
  border: none;
  border-bottom: 1px solid #fff;
  margin-top: 20px;
  width: 200px;
  font-size:18px;
}

#todo-container{
  width: 350px;
  margin: 50px auto auto auto;
  border:1px solid #fff;
  border-radius: 20px;
  padding: 10px;
  background-color: rgba(255, 255, 255, 0.2);
}

.todo-item{
  position : relative;
  padding: 10px;
  text-align: left;
  border-radius: 30px;
}

.todo-item *{
  display: inline;
}

.todo-item img{
  width: 25px;
  height: 25px;
}

.todo-item img:hover{
  opacity:1;
}

.todo-item img:first-child:hover ~ div{
  text-decoration: line-through;
}

.todo-item div{
  margin-left: 30px;
  font-size: 18px;
  position: absolute;
  top: 30%;
}


.todo-item :first-child{
  margin-left: 10px;
  border: 1px solid #fff;
  border-radius: 10px;
}

.todo-item :last-child{
  float: right;
  margin-right: 10px;
  opacity: .5;
}

.todo-item :first-child:hover{
  opacity:1;
  transform: scale(1.2);
  transition: .1s;
}

.clicked{
  opacity:.5;
  background-color: rgba(255, 255, 255, 0.7);
}

.check-line{
  text-decoration: line-through;
  opacity:.5;
}

#quotes{
  margin-top: 120px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
